<template>
  <div class="phoneDoor">
    <!--  -->
    <img
      width="60"
      class="phoneDoorStyle"
      :src="isOpening?loadingImg:phoneImg"
      @click="doorPress()"
    />
    <!--  -->
    <div
      v-if="isPopShow"
      :class="{themeL:bindThemeL}"
      class="phonedoor-popup-container"
      style="background: rgba(0, 0, 0,0.5);"
    >
      <div class="door-popup-body">
        <img :src="closeImg" class="popcloseBtn" @click="closePopup()" />

        <van-image fit="fill" :src="backImg" />
        <div id="qrArea" v-if="isLLQrcode">
          <div class="failText"></div>
          <div class="QRCode">
            <canvas id="phoneDoorQrcode" class="phoneDoorQrcode"></canvas>
          </div>
          <!-- <div class="QRCodeRefresh hide" style="position: absolute;bottom: 0;width: 100%;">
          <img
            style="position:relative;top: 2px"
            src="img/phonedoor/pic_tips_refresh.png"
            onclick="refreshQRcode()"
          />
          </div>-->

          <div class="lineBoxStyle">
            <div class="lineStyle"></div>
          </div>
        </div>
        <div class="opemDoorBox" v-show="isRemote">
          <div class="failMsg"></div>
          <div class="share" id="shareScrollbar">
            <div
              class="leftImg"
              v-for="(x,index) in doorList"
              :key="index"
              @click="doorRemoteOpen(x)"
            >
              <button class="doorBtn" v-if="x.openType>3">{{x.deviceName}}</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getDoorListService,
  openLinglingRemoteDoorService,
  addRecordService,
  addPointService,
  getLeeLenDoorInfoService,
  openLeeLenRemoteDoorService
} from "../../services/phoneDoorService";
import config from "@/common/config";
import QRCode from "qrcode"; //
import { store, storage } from "../../utils/storage";
import { mapState } from "vuex";
export default {
  name: "phoneDoor",
  data() {
    return {
      doorList: [], //
      isLLQrcode: false, //
      isLLBluetooth: false, //
      isLeeLenBluetooth: false, //
      isRemote: false, //
      llBluetoothKeys: "", //key
      llQrcodeKeys: "", //key
      linglingDoorInfos: [], //
      LLingID: "", //ID
      qrcode: "", //
      qrCanvas: null,
      isPopShow: false, //
      openStart: null, //
      isOpening: false, //
      popupTimer: null, //
      btnTimer: null,
      isCanvas: true,
      closeImg: require("../../assets/images/home/property/icon_phoneDoor_popup_close.png"),
      phoneImg: require("../../assets/images/home/property/icon_phoneDoor_open.png"),
      backImg: require("../../assets/images/home/property/icon_phoneDoor_popup_bg.png"),
      loadingImg: require("../../assets/images/home/property/icon_phoneDoor_loading.gif"),
      userInfo: {},
      /**/
      bindThemeL: this.$curTheme == 0 ? false : true
    };
  },
  computed: {
    //state
    phoneDoorRefresh() {
      return this.$store.state.home.phoneDoorRefresh;
    }
  },
  watch: {
    //state
    phoneDoorRefresh(val) {
      console.log("valvalvalvalvalvalvalvalval" + val);
      if (val) {
        this.$store.dispatch("setPhoneDoorRefreshFlag", false);
        this.doorList = [];
        this.isLLQrcode = false; //
        this.isLLBluetooth = false; //
        this.isLeeLenBluetooth = false; //
        this.isRemote = false; //
        this.llBluetoothKeys = ""; //key
        this.llQrcodeKeys = ""; //key
        this.linglingDoorInfos = []; //
        this.LLingID = ""; //ID
        this.qrcode = ""; //
        this.isPopShow = false; //
        this.openStart = null; //
        this.isOpening = false; //
        if (this.popupTimer != null) clearTimeout(this.popupTimer);
        if (this.btnTimer != null) clearTimeout(this.btnTimer);
        this.getDoorList();
      }
    }
  },
  mounted() {
    this.getDoorList();
  },
  methods: {
    /* ************************************************************************** */
    /*  */
    getDoorList() {
      console.log("getDoorList");
      let reqData = {
        areaCode: this.$userInfo.AREA_CODE,
        buildCode: this.$userInfo.BUILD_CODE,
        roomCode: this.$userInfo.ROOM_CODE,
        ownerCode: this.$userInfo.OWNER_CODE,
        unitNo: this.$userInfo.UNIT_NO
      };
      getDoorListService(reqData)
        .then(result => {
          console.log("findOpenType---result");
          console.log(JSON.stringify(result));
          //
          if (result.resultCode == 0) {
            this.doorList = result.data;
            this.findOpenType();
            this.$store.state.home.hasOpenDoor = true;
          }
        })
        .catch(error => {
          //
          this.phoneDoorDefaule = true; //default
          this.$toast("");
        });
    },
    /*  */
    findOpenType() {
      console.log("findOpenType");
      //
      for (let i in this.doorList) {
        let openType = this.doorList[i].openType;
        //1357,
        if ((openType & 1) == 1) {
          console.log("");
          if (this.doorList[i].deviceMfr == 2) {
            //deviceMfr=2 
            this.isLLBluetooth = true;
            //key
            this.llBluetoothKeys += this.doorList[i].sdkKey;
            this.llBluetoothKeys += ",";
          } else if (this.doorList[i].deviceMfr == 1) {
            //deviceMfr=1 
            this.isLeeLenBluetooth = true;
          }
        }

        //
        if ((2 & openType) == 2) {
          if (this.doorList[i].deviceMfr == 2) {
            console.log("");
            this.isLLQrcode = true;
            this.LLingID = this.doorList[i].linglingId;
            //key
            this.llQrcodeKeys += this.doorList[i].sdkKey + ",";
          }
        }
        //openType=4
        if ((4 & openType) == 4) {
          console.log("");
          this.isRemote = true;
        }
      }

      //
      this.llBluetoothKeys = this.llBluetoothKeys.substring(
        0,
        this.llBluetoothKeys.length - 1
      );
      this.llQrcodeKeys = this.llQrcodeKeys.substring(
        0,
        this.llQrcodeKeys.length - 1
      );
    },
    /* ************************************************************************** */
    /*  */
    doorPress() {
      //
      if (this.doorList == null || this.doorList.length == 0) {
        this.$toast("!");
        this.closePopup();
        return;
      }
      //
      if(this.isOpening){
        this.$toast("!");
        return;
      }else{
        this.isOpening = true;
      }
      // +
      this.getCurrentLocatioon();
      console.log("doorPress");
      this.bindThemeL = this.$curTheme == 0 ? false : true;
      //
      this.openStart = new Date();
      //
      this.btnTimer = setTimeout(() => {
        this.isOpening = false;
      }, 8000);
      //
      setTimeout(() => {
        //
        if (this.isLLQrcode || this.isRemote) {
          this.showRemotePopup();
        }
      }, 2000);
      //
      if (this.isLLBluetooth) {
        this.openLLBlueTooth();
      }
      //
      if (this.isLeeLenBluetooth) {
        this.openLeeLenBlueTooth();
      }
    },
    /* ************************************************************************** */
    /*
     * 
     */
    getCurrentLocatioon() {
      //
      AMap.plugin("AMap.Geolocation", () => {
        //
        let geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, //:true
          timeout: 10000, //10
          maximumAge: 0, //00
          convert: true, //true
          showButton: false, //true
          buttonPosition: "LB", //'LB'
          buttonOffset: new AMap.Pixel(10, 20), //Pixel(10, 20)
          showMarker: false, //true
          showCircle: false, //true
          panToLocation: true, //true
          zoomToAccuracy: true //false
        });
        geolocation.getCurrentPosition();
        AMap.event.addListener(geolocation, "complete", res => {
          console.log("complete" + JSON.stringify(res));
          //
          this.longitude = res.position.lng;
          this.latitude = res.position.lat;

          // 
          // 10
          this.doorList.forEach(door => {
            //
            if (door.deviceLatitude && door.deviceLongitude) {
              //
              var p1 = [this.longitude, this.latitude];
              var p2 = [door.deviceLongitude, door.deviceLatitude];
              let far = AMap.GeometryUtil.distance(p1, p2);
              console.log("far = "+far);
              //10
              if (far < 10) {
                if (door.deviceMfr == 2) {
                  this.openLinglingRemoteDoor(door,true);
                } else {
                  this.openLeeLenRemoteDoor(door,true);
                }
              }
            }
          });
        }); //
        AMap.event.addListener(geolocation, "error", err => {
          console.log("error" + err);
        }); //
      });
    },
    /* ************************************************************************** */
    /*
     * 
     */
    openLLBlueTooth() {
      console.log("======this.llBluetoothKeys=====" + this.llBluetoothKeys);
      var lingling = cordova.plugins.linglingPlugin;
      lingling.btOpenDoorMethod(
        this.llBluetoothKeys,
        resultStatus => {
          console.log("======");
          this.closePopup();

          this.$toast({
            message: "",
            onClose: () => {
              //   
              for (var i = 0; i < this.doorList.length; i++) {
                console.log("for resultStatus=" + resultStatus);
                // 
                if (this.doorList[i].deviceCode == resultStatus) {
                  console.log(
                    " deviceCode=" + this.doorList[i].deviceCode
                  );
                  //  
                  this.addRecord(
                    this.doorList[i].deviceCode,
                    this.doorList[i].deviceName,
                    1,
                    2,
                    this.doorList[i].buildCode == "" ? 0 : 1,
                    "",
                    1,
                    ""
                  );

                  this.addPoint();
                  return;
                }
              }
            }
          });
        },
        message => {
          console.log("" + message);
          // 
          this.addRecord("", "", 1, 2, 3, "", 0, message);
          if (this.isLeeLenBluetooth) {
            this.openLeeLenBlueTooth();
            return;
          }
          if (!this.isRemote && !this.isLLQrcode) {
            this.closePopup();
            this.$toast("");
            return;
          }
        }
      );
    },
    /*  */
    openLeeLenBlueTooth() {
      var reqData = {
        areaCode: this.$userInfo.AREA_CODE,
        buildCode: this.$userInfo.BUILD_CODE,
        roomCode: this.$userInfo.ROOM_CODE,
        ownerCode: this.$userInfo.OWNER_CODE,
        unitNo: this.$userInfo.UNIT_NO
      };
      getLeeLenDoorInfoService(reqData)
        .then(res => {
          if (res.resultCode == 0) {
            var llData = JSON.parse(JSON.stringify(res.data));
            var obj = {
              cardNo: llData.cardNo,
              useType: llData.userType,
              deviceList: llData.leeLenBluetoothDoorInfoVOS,
              isLLBluetooth: this.isLeeLenBluetooth
            };
            if (/android/.test(navigator.userAgent.toLowerCase())) {
              this.leelenAndroid(obj);
            } else {
              this.leelenIos(obj);
            }
          }else{
            this.addRecord("", "", 1, 1, 3, "", 0, res.msg);
          }
        })
        .catch(err => {
          console.log(JSON.stringify(err));
          this.$toast("");
        });
    },
    leelenIos(obj) {
      /*  */
      window.broadcaster.fireNativeEvent(
        "openLeelenDoor",
        { openCode: obj },
        function() {
          console.log("");
        }
      );

      var leelenBlueFlag = 0;
      // ;
      window.broadcaster.addEventListener("LeeLenResultCode", data2 => {
        if (data2.code == "0" && leelenBlueFlag == 0) {
          this.$toast({
            message: "",
            onClose: () => {
              let buildCode = "";
              let door = null;
              for (var i = 0; i < this.doorList.length; i++) {
                if (this.doorList[i].deviceNo == data2.DEVICE_ID) {
                  buildCode = this.doorList[i].buildCode;
                  door = this.doorList[i];
                }
              }
              //  
              this.addRecord(
                door.deviceCode,
                door.deviceName,
                1,
                1,
                buildCode == "" ? 0 : 1,
                "",
                1,
                ""
              );

              this.addPoint();
            }
          });

          this.closePopup();
        } else if (leelenBlueFlag == 0) {
          console.log("");
          //  
          var errMsg = this.getLeelenErrorMsg(data2.code);
          this.addRecord("", "", 1, 1, 3, "", 0, errMsg);

          if (!this.isRemote && !this.isLLQrcode) {
            this.closePopup();
            this.$toast("");
            return;
          }
        }
        leelenBlueFlag = 1;
      });
    },
    leelenAndroid(obj) {
      var leelen = cordova.plugins.LeeLenPlugin;
      leelen.access(
        obj,
        resultStatus => {
          console.log("line 335!!!!!_+_+_+_+_ " + resultStatus);
          this.$toast({
            message: "",
            onClose: () => {
              //
              let buildCode = "";
              let door = null;
              for (var i = 0; i < this.doorList.length; i++) {
                if (this.doorList[i].deviceNo == resultStatus.deviceId) {
                  buildCode = this.doorList[i].buildCode;
                  door = this.doorList[i];
                }
              }
              this.addRecord(
                door.deviceCode,
                door.deviceName,
                1,
                1,
                buildCode == "" ? 0 : 1,
                "",
                1,
                ""
              );

              this.addPoint();
            }
          });

          this.closePopup();
        },
        message => {
          console.log("===countFlag2=== " + message);
          //  
          var errMsg = this.getLeelenErrorMsg(message);
          this.addRecord("", "", 1, 1, 3, "", 0, errMsg);
          if (!this.isRemote && !this.isLLQrcode) {
            this.closePopup();
            this.$toast("");
            return;
          }
        }
      );
    },
    getLeelenErrorMsg(message) {
      var errMsg = "";
      switch (message) {
        case "10001":
          errMsg = "(:10001)";
          break;
        case "10002":
          errMsg = "(:10002)";
          break;
        case "10003":
          errMsg = "(:10003)";
          break;
        case "10004":
          errMsg = "(:10004)";
          break;
        case "10005":
          errMsg = "(:10005)";
          break;
        case "10006":
          errMsg = "(:10006)";
          break;
        case "10007":
          errMsg = "(:10007)";
          break;
      }
      return errMsg;
    },
    /* ************************************************************************** */
    showRemotePopup() {
      this.isPopShow = true;
      //
      this.$nextTick(() => {
        this.getQrCanvas();
      });

      this.popupTimer = setTimeout(() => {
        this.addPoint();
        this.closePopup();
      }, 15000);
    },
    getQrCanvas() {
      if (this.llQrcodeKeys != "" && this.llQrcodeKeys != null) {
        this.qrCanvas = document.getElementById("phoneDoorQrcode");
        console.log("canvas=" + this.qrCanvas);
        var lingling = cordova.plugins.linglingPlugin;
        lingling.getQrCode(
          this.LLingID,
          this.llQrcodeKeys,
          resultStatus => {
            console.log("" + resultStatus);
            // valmsgcanvas
            console.log("sdkKeys=" + this.llQrcodeKeys);
            QRCode.toCanvas(this.qrCanvas, resultStatus);
          },
          function(message) {
            console.log("");
          }
        );
      }
    },
    closePopup() {
      clearTimeout(this.popupTimer);
      clearTimeout(this.btnTimer);
      this.isOpening = false;
      this.isPopShow = false;
    },
    /*  */
    doorRemoteOpen(obj) {
      if (obj.deviceMfr == 2) {
        this.openLinglingRemoteDoor(obj);
      } else {
        this.openLeeLenRemoteDoor(obj);
      }
    },
    /*  */
    openLinglingRemoteDoor(door,isGPS=false) {
      let reqData = {
        sdkKey: door.sdkKey
      };
      openLinglingRemoteDoorService(reqData)
        .then(result => {
          console.log(JSON.stringify(result));
          if (result.resultCode == 0) {
            //
            this.closePopup();
            this.$toast({
              message: "",
              onClose: () => {
                this.addRecord(
                  door.deviceCode,
                  door.deviceName,
                  2,
                  2,
                  door.buildCode == "" ? 0 : 1,
                  "",
                  1,
                  isGPS?"(GPS)":""
                );
                this.addPoint();
              }
            });
          } else {
            //
            this.closePopup();
            this.$toast({
              message: result.msg,
              onClose: () => {
                this.addRecord(
                  door.deviceCode,
                  door.deviceName,
                  2,
                  2,
                  door.buildCode == "" ? 0 : 1,
                  "",
                  0,
                  result.msg
                );
              }
            });
          }
        })
        .catch(error => {
          this.$toast("");
        });
    },
    /*  */
    openLeeLenRemoteDoor(door,isGPS=false) {
      let reqData = {
        areaNo: door.areaNo,
        deviceNo: door.deviceNo,
        deviceCode: door.deviceCode,
        readHeadNo: door.readHeadNo
      };
      openLeeLenRemoteDoorService(reqData)
        .then(result => {
          if (result.resultCode == 0) {
            //
            this.closePopup();
            console.log("");
            this.$toast({
              message: "",
              onClose: () => {
                console.log("toast close");
                this.addRecord(
                  door.deviceCode,
                  door.deviceName,
                  2,
                  1,
                  door.buildCode == "" ? 0 : 1,
                  "",
                  1,
                  isGPS?"(GPS)":""
                );

                this.addPoint();
              }
            });
          } else {
            //
            this.closePopup();
            this.$toast({
              message: "",
              onClose: () => {
                this.addRecord(
                  door.deviceCode,
                  door.deviceName,
                  2,
                  1,
                  door.buildCode == "" ? 0 : 1,
                  "",
                  0,
                  result.msg
                );
              }
            });
          }
        })
        .catch(error => {
          this.$toast("");
        });
    },

    /* ************************************************************************** */
    /*  */
    addRecord(
      deviceId,
      deviceName,
      openType,
      deviceMfr,
      doorType,
      duration,
      openSucceed,
      remarks
    ) {
      //
      var phoneModel = device.model;
      var phoneType = device.platform;

      //
      var openEnd = new Date();
      duration = openEnd.getTime() - this.openStart.getTime();
      let reqData = {
        areaCode: this.$userInfo.AREA_CODE, //
        areaName: this.$userInfo.AREA_NAME, //
        buildCode: this.$userInfo.BUILD_CODE, //
        buildName: this.$userInfo.BUILD_NAME, //
        unitNo: this.$userInfo.UNIT_NO, //
        roomCode: this.$userInfo.ROOM_CODE, //
        ownerCode: this.$userInfo.OWNER_CODE, //
        ownerName: this.$userInfo.OWNER_NAME, //
        ownerNickName: this.$userInfo.OWNER_NICK_NAME, //
        phone: this.$userInfo.PHONE, //
        phoneModel: phoneModel, // 
        phoneType: phoneType, // 
        deviceId: deviceId, // 
        deviceName: deviceName, // 
        openType: openType, // 1230
        deviceMan: deviceMfr, // 0 1 2 3 4
        doorType: doorType, // 0 1
        duration: duration, //
        openSucceed: openSucceed, // 0 1
        remarks: remarks //
      };
      if (this.$userInfo.AREA_NO == "" || this.$userInfo.AREA_NO == undefined) {
        // AREA_NO
        reqData.DOOR_TYPE = "1";
      } else {
        reqData.DOOR_TYPE = "0"; //0,1;
      }
      reqData.DOOR_TYPE = "0";
      //  3.2.4.0
      if (openType == 1 && openSucceed == 1) {
        reqData.deviceLongitude = this.longitude;
        reqData.deviceLatitude = this.latitude;
      }
      addRecordService(reqData)
        .then(result => {
          //
          if (result.success) {
            //
          }
        })
        .catch(error => {
          //
          this.phoneDoorDefaule = true; //default
        });
    },
    /*  */
    addPoint() {
      var reqData = {
        areaCode: this.$userInfo.AREA_CODE,
        behaviourNo: "1002",
        ownerCode: this.$userInfo.OWNER_CODE,
        appVersion: config.version
      };
      addPointService(reqData)
        .then(result => {
          //
          if (result.code == 200) {
            this.$toast("" + result.data + "");
          }
        })
        .catch(error => {
          //
          this.phoneDoorDefaule = true; //default
          this.$toast("");
        });
    }
    /* ************************************************************************** */
  }
};
</script>

<style scoped lang="scss">
.themeL {
  .popcloseBtn {
    width: 28px;
    height: 28px;
  }
  .door-popup-body {
    .failText {
      font-size: 18px;
    }
    .phoneDoorQrcode {
      height: 190px;
      width: 190px;
    }
    .failMsg {
      font-size: 18px;
    }
    .share {
      padding: 0 4% 0 4%;
    }
    .share .leftImg .doorBtn {
      width: 125px;
      height: 40px;
      border-radius: 500px;
      font-size: 14px;
    }
    .share .leftImg .doorBtn:active {
      width: 125px;
      height: 40px;
      border-radius: 500px;
      font-size: 14px;
    }
  }
}
.phoneDoor {
  width: 60px;
  text-align: center;
  height: 60px;
}
.phoneDoorStyle {
  position: absolute;
  top: 0;
  left: 0;
}
.door-popup-body {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 80%;
  transform: translate(-50%, -50%);
  /* height: 70%; */
  border-radius: 10px;
  /*margin: 0 auto;*/
  /*margin-top: 35%;*/
  text-align: center;
  background-color: white;
  padding-bottom: 20px;
}
.door-popup-body .titleImg {
  width: 100%;
  position: relative;
}
.door-popup-body .titleImg img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
}
.popcloseBtn {
  position: absolute;
  width: 20px;
  height: 20px;
  right: -10px;
  top: -10px;
  z-index: 100;
}

.door-popup-body .share {
  margin-top: 5px;
  display: flex;
  justify-content: space-between;
  padding: 0 12% 0 12%;
  flex-wrap: wrap;
  max-height: 100px;
  overflow: auto;
}
.door-popup-body .share .leftImg {
  display: flex;
}
.door-popup-body .share .leftImg .doorBtn:active {
  color: white;
  background: #fb5768;
  border: 1px solid #fb5768;
  border-radius: 20px;
  height: 30px;
  margin-left: 1px;
  margin-top: 10px;
  width: 100px;
  font-size: 12px;
}
.door-popup-body .share .leftImg .doorBtn {
  background: #ffffff;
  border: 1px solid #979797;
  border-radius: 20px;
  height: 30px;
  margin-left: 1px;
  margin-top: 10px;
  width: 100px;
  font-size: 12px;
  color: #333;
}
.door-popup-body .opemDoorBox {
  margin-top: 10px;
}

.door-popup-body .failText {
  margin-top: 10px;
  width: 100%;
  top: 27.5%;
  font-size: 15px;
  color: #1a1a1a;
  letter-spacing: 0.06px;
  text-align: center;
}

.door-popup-body .failMsg {
  font-family: pingfangSC-Medium;
